<script>
//import HelloWorld from "./components/HelloWorld.vue";
import img from "./assets/img.js";
//import { log } from 'util';
export default {
  name: "app",
  components: {},
  data: function() {
    return {
      cardWidth:
        window.innerWidth > 700 ? 680 + "px" : window.innerWidth * 0.9 + "px",
      logoWidht: 150,
      clickonce: true
    };
  },
  methods: {
    calculate: function() {
      var gwgz = Number(document.getElementById("salary_1").value);
      var xjgz = Number(document.getElementById("salary_2").value);
      var zwbt = Number(document.getElementById("salary_3").value);

      var jxkj = Math.abs(Number(document.getElementById("salary_3-").value));

      var gwjt = Number(document.getElementById("salary_4").value);
      var ksjt = Number(document.getElementById("salary_5").value);
      var ylbx = Number(document.getElementById("salary_6").value);
      var zynj = Number(document.getElementById("salary_7").value);
      var gjj = Number(document.getElementById("salary_8").value);
      var bx = Number(document.getElementById("salary_9").value);
      var gs = Number(document.getElementById("salary_10").value);
      var jishu = Number(
        gwgz + xjgz + zwbt - jxkj + gwjt + ksjt - ylbx - zynj - gjj - bx - gs
      );
      var basicAmount = jishu;
      var Party_membership_dues = 0;
      if (basicAmount <= 3000) {
        Party_membership_dues = basicAmount * 0.005;
      } else if (basicAmount <= 5000) {
        Party_membership_dues = basicAmount * 0.01;
      } else if (basicAmount <= 10000) {
        Party_membership_dues = basicAmount * 0.015;
      } else {
        Party_membership_dues = basicAmount * 0.02;
      }
      document.getElementById("end").value = parseInt(Party_membership_dues);
      document.getElementById("end0").value = parseInt(jishu);
      this.clickonce = true;
    }
  },
  mounted: function() {
    //var background = document.getElementById("app");

    //background.style.backgroundImage = `url(${img.background_red})`;

