    window.onresize = function windowResize() {
      // 通过捕获系统的onresize事件触发我们需要执行的事件
      if (window.innerWidth > 800) {
        this.myWidth = 480 + "px";
        this.logoWidht = 400 + "px";
      }
    };
  }
};
</script>

